import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import PropTypes from 'prop-types';
import AppBar from 'material-ui/AppBar';
import { Tabs, Tab } from 'material-ui/Tabs';
import SwipeableViews from 'react-swipeable-views';
import ProjectCard from './Card';
const img1 = require('./phone4.png');
const img2 = require('./phone2.png');
const img3 = require('./phone3.png');

const images = [
	img1,
	img2,
	img3
];

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			slideIndex: 0,
		};
	}

	handleChange = (value) => {
		this.setState({
			slideIndex: value,
		});
	};
	render() {
		return (
			<MuiThemeProvider>
				<AppBar
					title="Ramona Bellamy's Portfolio"
					showMenuIconButton={false}
					style={{
						textAlign: 'left',
						backgroundColor: '#212121',
						height: 40,
					}}
				/>

				<Tabs
					inkBarStyle={{backgroundColor: '#fff'}}
					onChange={this.handleChange}
					value={this.state.slideIndex}
				>
					<Tab style={{ backgroundColor: '#4DB6AC' }} label="Cocktail Search" value={0} />
					<Tab style={{ backgroundColor: '#4DB6AC' }} label="Muse" value={1} />
					<Tab style={{ backgroundColor: '#4DB6AC' }} label="Task Tracker" value={2} />
				</Tabs>
				<SwipeableViews
					index={this.state.slideIndex}
					onChangeIndex={this.handleChange}
				>
					<div style={{ paddingBottom: '100px' }}>
						<ProjectCard
							title="Cocktail Search"
							link='https://cocktail-search.surge.sh'
							github='https://github.com/mona87/cocktail-finder'
							bgColor='#E0F7FA'
							text='Cocktail search allows users to search  and save their favorite drinks using the thecocktaildb.com api.'
							libs='React, Redux, Material-UI,'
							img={images[0]} />
					</div>
					<div >
						<ProjectCard
							title="Muse"
							bgColor='#FFEBEE'
							link='https://muse-mix.surge.sh'
							github='https://github.com/mona87/muse'
							text={`Muse allows users to search and preview artists' songs using Spotify's Web Api.`}
							libs='React, Redux, Material-UI, Spotify-Web API'
							img={images[1]} />
					</div>
					<div >
						<ProjectCard
							title="Task Tracker"
							bgColor='#F1F8E9'
							link='https://tracker-app.surge.sh'
							github='https://github.com/mona87/tracker'
							text='Task Tracker is a task manager which allows users to sign up and create tasks. Users also have the ability to see and delete tasks created by other users.'
							libs='React, Redux, React-Router, Material-UI, Firebase'
							img={images[2]} />
					</div>
				</SwipeableViews>
			</MuiThemeProvider>
		)
	}
}


export default App;



MuiThemeProvider.propTypes = {
	children: PropTypes.array
};


