import React, { Component } from 'react';
import { Card, CardActions, CardMedia, CardTitle, CardText } from 'material-ui/Card';
import FlatButton from 'material-ui/FlatButton';
import Paper from 'material-ui/Paper';


const style = {

	width: '70%',
	margin: 'auto',
	display: 'block',
	marginTop: '70px',
};



class ProjectCard extends Component {
	render() {
		return (
			<Paper className='paper-style' style={style} zDepth={3} >
				<Card>
					<CardTitle
						titleColor='#fff'
						style={{ backgroundColor: '#212121' }}
						title={this.props.title} />

					<CardMedia
						style={{ backgroundColor: this.props.bgColor, cursor: 'pointer' }}
						onClick={() => { window.open(this.props.link, '_blank'); }}
					>
						<img
							src={this.props.img}
							style={{
								width: '200px',
								minWidth: '0',
								padding: '20px 0',
								margin: 'auto',
								display: 'block'
							}} alt="remindme" />
					</CardMedia>

					<CardText
						style={{
							backgroundColor: '#212121',
							color: '#fff', wordSpacing: '1px',
							lineHeight: '20px'
						}}
					>
						<p>
							{this.props.text}
						</p>
						<span>
							Libraries: {this.props.libs}
						</span>

					</CardText>
					<CardActions
						style={{ backgroundColor: '#212121', color: '#fff' }}

					>
						<FlatButton
							style={{ color: '#ff4081' }}
							label="Link"
							onClick={() => { window.open(this.props.link, '_blank'); }}
						/>
						<FlatButton
							style={{ color: '#ff4081' }}
							label="Github"
							onClick={() => { window.open(this.props.github, '_blank'); }}
						/>
					</CardActions>
				</Card>
			</Paper>
		)
	}


}

export default ProjectCard;


